/**
 * Blank Layout with consistent prop naming and standardized size variants
 * @module @voilajsx/uikit
 * @file src/components/layouts/blank.tsx
 */

import * as React from 'react';
import { forwardRef } from 'react';
import { cva } from 'class-variance-authority';
import { cn } from '@/lib/utils';
import type { Size } from '@/types';

/**
 * Blank layout variants
 */
const blankVariants = cva(
  'min-h-screen bg-background flex items-center justify-center p-4',
  {
    variants: {
      variant: {
        default: '',
        card: '',
        error: '',
        maintenance: '',
        suspension: ''
      }
    },
    defaultVariants: {
      variant: 'default'
    }
  }
);

/**
 * Content container variants based on size and variant
 */
const contentVariants = cva(
  'w-full space-y-8 text-center',
  {
    variants: {
      size: {
        sm: 'max-w-md',
        md: 'max-w-2xl',
        lg: 'max-w-4xl',
        xl: 'max-w-5xl',
        full: 'max-w-6xl'
      },
      variant: {
        default: '',
        card: 'p-8 sm:p-12 bg-card border border-border rounded-xl shadow-lg',
        error: '',
        maintenance: '',
        suspension: ''
      }
    },
    defaultVariants: {
      size: 'md',
      variant: 'default'
    }
  }
);

/**
 * Icon variant styles
 */
const iconVariants = cva(
  'flex justify-center',
  {
    variants: {
      variant: {
        default: '',
        card: '',
        error: 'text-muted-foreground',
        maintenance: 'text-primary animate-pulse',
        suspension: 'text-destructive'
      }
    },
    defaultVariants: {
      variant: 'default'
    }
  }
);

/**
 * Title variant styles
 */
const titleVariants = cva(
  'font-bold tracking-tight text-foreground',
  {
    variants: {
      size: {
        sm: 'text-2xl sm:text-3xl',
        md: 'text-3xl sm:text-4xl',
        lg: 'text-4xl sm:text-5xl',
        xl: 'text-5xl sm:text-6xl',
        full: 'text-6xl sm:text-7xl'
      }
    },
    defaultVariants: {
      size: 'md'
    }
  }
);

/**
 * Subtitle variant styles
 */
const subtitleVariants = cva(
  'text-muted-foreground',
  {
    variants: {
      size: {
        sm: 'text-base',
        md: 'text-lg',
        lg: 'text-xl',
        xl: 'text-2xl',
        full: 'text-3xl'
      },
      variant: {
        default: 'max-w-md mx-auto',
        card: '',
        error: '',
        maintenance: '',
        suspension: ''
      }
    },
    defaultVariants: {
      size: 'md',
      variant: 'default'
    }
  }
);

/**
 * Footer variant styles
 */
const footerVariants = cva(
  'text-center',
  {
    variants: {
      variant: {
        default: '',
        card: '',
        error: 'text-sm text-muted-foreground',
        maintenance: 'text-sm text-muted-foreground',
        suspension: 'text-sm text-muted-foreground'
      }
    },
    defaultVariants: {
      variant: 'default'
    }
  }
);

/**
 * Blank Layout props
 */
export interface BlankLayoutProps {
  /** Blank layout variant */
  variant?: 'default' | 'card' | 'error' | 'maintenance' | 'suspension';
  /** Content container size */
  size?: Size;
  /** Page title */
  title?: string;
  /** Page subtitle */
  subtitle?: string;
  /** Logo component */
  logo?: React.ReactNode;
  /** Icon component (e.g., error icon) */
  icon?: React.ReactNode;
  /** Action buttons */
  actions?: React.ReactNode;
  /** Footer content */
  footer?: React.ReactNode;
  /** Additional CSS classes */
  className?: string;
  /** Container props for customization */
  containerProps?: React.HTMLAttributes<HTMLDivElement>;
  /** Main content */
  children?: React.ReactNode;
}

/**
 * BlankLayout - Unified blank page layout with standardized props
 */
const BlankLayoutComponent = forwardRef<HTMLDivElement, BlankLayoutProps>(({
  variant = 'default',
  size = 'md',
  title,
  subtitle,
  logo,
  icon,
  actions,
  footer,
  className,
  containerProps,
  children,
}, ref) => {

  return (
    <div ref={ref} className={cn(blankVariants({ variant }), className)}>
      <div
        {...containerProps}
        className={cn(
          contentVariants({ size, variant }),
          containerProps?.className
        )}
      >
        {/* Logo Section */}
        {logo && (
          <div className="flex justify-center">
            {logo}
          </div>
        )}

        {/* Icon Section */}
        {icon && (
          <div className={cn(iconVariants({ variant }))}>
            {icon}
          </div>
        )}

        {/* Title and Subtitle */}
        {(title || subtitle) && (
          <div className="space-y-4">
            {title && (
              <h1 className={cn(titleVariants({ size }))}>
                {title}
              </h1>
            )}
            {subtitle && (
              <p className={cn(subtitleVariants({ size, variant }))}>
                {subtitle}
              </p>
            )}
          </div>
        )}

        {/* Main Content */}
        {children && (
          <div className="space-y-6">
            {children}
          </div>
        )}

        {/* Action Buttons */}
        {actions && (
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            {actions}
          </div>
        )}

        {/* Footer */}
        {footer && (
          <div className={cn(footerVariants({ variant }))}>
            {footer}
          </div>
        )}
      </div>
    </div>
  );
});

BlankLayoutComponent.displayName = 'BlankLayout';

/**
 * BlankLayout with standardized props
 */
const BlankLayout = BlankLayoutComponent;

export { BlankLayout };